@import url(https://fonts.googleapis.com/css2?family=Karla:wght@500&display=swap);

.App {
  text-align: center;
  font-family: "Modak", cursive;
}

.App-link {
  color: #61dafb;
}
.App-header {
  background-color: #fff2e0;
  min-height: 100vh;
  min-width: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.name {
  display: inline;
  font-size: 130px;
}

.school {
  font-size: 28px;
}

.links {
  font-size: 1.2rem;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.scroll {
  text-align: center;
  margin: 8% 0;
}

.bounce {
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
.my-navbar {
  background-color: #fff2e0;
}

.navbar-header {
  background-color: white;
}

.navbar-title {
  font-size: 1.5rem;
  text-decoration: none !important;
}

.navbar-option,
.navbar-title {
  color: black !important;
}

.navbar-option:hover,
.navbar-title:hover {
  color: red !important;
}

.my-active .navbar-title {
  color: red !important;
}

.my-active .lead navbar-option {
  color: red !important;
}

.my-active .nav-link {
  color: red !important;
}

.navbar-toggle {
  color: black !important;
}

.nav-link {
  color: black !important;
}
.btn {
  height: 5vh;
  width: 8vw;
  display: block;
  border: 2px solid;
  border-bottom-width: 4px;
  font: inherit;
  letter-spacing: inherit;
  text-transform: inherit;
  transition: color 1s;
}

.btn-4 {
  color: #eea163;
}
.btn-4:hover {
  animation: zigzag 1s linear infinite;
  background: linear-gradient(135deg, rgba(238, 161, 99, 0.25) 0.25em, transparent 0.25em) -0.5em 0, linear-gradient(225deg, rgba(255, 21, 0, 0.25) 0.25em, transparent 0.25em) -0.5em 0, linear-gradient(315deg, rgba(238, 161, 99, 0.25) 0.25em, transparent 0.25em) 0 0, linear-gradient(45deg, rgba(238, 161, 99, 0.25) 0.25em, transparent 0.25em) 0 0;
  background-size: 0.75em 0.75em;
  color: #63b0ee;
}

@keyframes zigzag {
  100% {
    background-position: 1em 0, 1em 0, -0.75em 0, -0.75em 0;
  }
}

@keyframes halftone {
  100% {
    background-size: 2.375em 2.375em, 0.1em 0.1em;
  }
}

@keyframes stripes-move {
  100% {
    background-position: 5em 0px;
  }
}

@keyframes stripes-move {
  100% {
    background-position: 5em 0px;
  }
}
@keyframes animatedBackground {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 100% 0;
  }
}

@keyframes colorRotate {
  100% {
    -webkit-filter: hue-rotate(360deg);
  }
}

#about {
  background-image: url(/static/media/about-bg.020d839f.jpg);
  background-blend-mode: overlay;
  background-position: 0px 0px;
  background-repeat: repeat-x;
  animation: animatedBackground 10s linear infinite alternate;
}

